import React, { useEffect } from 'react';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import PropTypes from 'prop-types';
import {useSelector} from 'react-redux'

import {
  Avatar,
  Box,
  Button,
  Divider,
  Drawer,
  Hidden,
  List,
  Typography,
  makeStyles
} from '@material-ui/core';
import {
  BarChart as BarChartIcon,
  Star as StarIcon,
  Calendar as CalendarIcon,
  User as UserIcon,
  Layers as LayersIcon,
  Users as UsersIcon,
  Tag as TagsIcon,
  Activity as ActivityIcon,
} from 'react-feather';
import NavItem from './NavItem';



const items = [
  {
    href: '/dashboard',
    icon: BarChartIcon,
    title: 'Dashboard'
  },
  {
    href: '/dashboard/events',
    icon: CalendarIcon,
    title: 'Events'
  },
  {
    href: '/dashboard/categories',
    icon: TagsIcon,
    title: 'Categories'
  },
  {
    href: '/dashboard/speakers',
    icon: UsersIcon,
    title: 'Speakers'
  },
  {
    href: '/dashboard/sponsors',
    icon: StarIcon,
    title: 'Sponsors'
  },
  {
    href: '/dashboard/tickets',
    icon: LayersIcon,
    title: 'Tickets'
  },
  {
    href: '/dashboard/transactions',
    icon: ActivityIcon,
    title: 'Transactions'
  },
  {
    href: '/dashboard/users',
    icon: UserIcon,
    title: 'Users'
  }
];

const useStyles = makeStyles(() => ({
  mobileDrawer: {
    width: 256
  },
  desktopDrawer: {
    width: 256,
    top: 64,
    height: 'calc(100% - 64px)'
  },
  avatar: {
    cursor: 'pointer',
    width: 64,
    height: 64
  }
}));

const NavBar = ({ onMobileClose, openMobile }) => {
  const classes = useStyles();
  const location = useLocation();
  const auth = useSelector(state => state.auth);


  const user = {
    avatar: auth.user.avatarUrl,
    jobTitle: 'Admin',
    name: auth.user.name
  };

  
  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.pathname]);

  const content = (
    <Box
      height="100%"
      display="flex"
      flexDirection="column"
    >
      <Box
        alignItems="center"
        display="flex"
        flexDirection="column"
        p={2}
      >
        <Avatar
          className={classes.avatar}
          component={RouterLink}
          src={user.avatar}
          to="/admin/account"
        />
        <Typography
          className={classes.name}
          color="textPrimary"
          variant="h5"
        >
          {user.name}
        </Typography>
        <Typography
          color="textSecondary"
          variant="body2"
        >
          {user.jobTitle}
        </Typography>
      </Box>
      <Divider />
      <Box p={2}>
        <List>
          {items.map((item) => (
            <NavItem
              href={item.href}
              key={item.title}
              title={item.title}
              icon={item.icon}
            />
          ))}
        </List>
      </Box>
     
      </Box>
   
  );

  return (
    <>
      <Hidden lgUp>
        <Drawer
          anchor="left"
          classes={{ paper: classes.mobileDrawer }}
          onClose={onMobileClose}
          open={openMobile}
          variant="temporary"
        >
          {content}
        </Drawer>
      </Hidden>
      <Hidden mdDown>
        <Drawer
          anchor="left"
          classes={{ paper: classes.desktopDrawer }}
          open
          variant="persistent"
        >
          {content}
        </Drawer>
      </Hidden>
    </>
  );
};

NavBar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

NavBar.defaultProps = {
  onMobileClose: () => {},
  openMobile: false
};

export default NavBar;
